import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-score.scss';
import '@servicenow/now-chart-sparkline';
import {
	commonView,
	renderScore,
	renderChange,
	renderTargetGap
} from './utils/common';
import {
	seriesConfigSchema,
	thresholdConfigSchema,
	thresholdDataPointSchema,
	targetSeriesConfigSchema
} from '@servicenow/now-chart-sparkline';
import {configAriaSchema} from '@servicenow/library-schemas';

const objHasProps = (obj) => {
	if (!obj) return false;

	for (let prop in obj) {
		if (Object.prototype.hasOwnProperty.call(obj, prop)) {
			return true;
		}
	}

	return false;
};

/*
  The largest score size overlaps the top heading so we cannot hover to get the tooltip. So we use the relative/absolute trick to put it on top of the score so we can. But then sizing becomes an issue, so we put an empty space with line-height-crop to match the space taken up by the heading.
*/
const renderHeadingBlock = (hideWhitespace, heading) => {
	if (hideWhitespace && !heading) {
		return null;
	}

	return (
		<span className="now-score-heading -top">
			<span className="will-truncate">{heading}</span>
		</span>
	);
};

const renderChangeBlock = (hideWhitespace, changeInfo) => {
	if (hideWhitespace && !objHasProps(changeInfo)) {
		return null;
	}

	return (
		<span className="now-m-block-start--md">{renderChange(changeInfo)}</span>
	);
};

const renderTargetBlock = (hideWhitespace, targetInfo) => {
	if (hideWhitespace && !objHasProps(targetInfo)) {
		return null;
	}

	return (
		<span className="now-m-block-start--lg">{renderTargetGap(targetInfo)}</span>
	);
};

const renderSparklineBlock = (hideWhitespace, sparkline, configAria) => {
	const hasSparkline = objHasProps(sparkline);
	if (hideWhitespace && !hasSparkline) {
		return null;
	}

	const configAriaToUse = (configAria && configAria.sparkline) || configAria;

	return (
		<span
			class={{
				'now-chart-sparkline-container': true,
				'has-chart': hasSparkline
			}}>
			{hasSparkline ? (
				<now-chart-sparkline
					className="now-chart-sparkline"
					width="auto"
					height="auto"
					configAria={configAriaToUse}
					{...sparkline}
				/>
			) : (
				undefined
			)}
		</span>
	);
};

const renderTimestampBlock = (hideWhitespace, timestamp) => {
	if (hideWhitespace && !timestamp) {
		return null;
	}

	return (
		<span
			class={{
				'now-score-timestamp': true,
				'is-expanded': !hideWhitespace
			}}>
			<span className="will-truncate">{timestamp}</span>
		</span>
	);
};

const renderScoreAdvanced = (props) => {
	const {
		heading,
		score,
		scoreSize,
		changeInfo,
		targetInfo,
		timestamp,
		sparkline,
		configAria,
		hideWhitespace
	} = props;

	return (
		<Fragment>
			{renderHeadingBlock(hideWhitespace, heading)}
			{renderScore(score, scoreSize)}
			{renderChangeBlock(hideWhitespace, changeInfo)}
			{renderTargetBlock(hideWhitespace, targetInfo)}
			{renderSparklineBlock(hideWhitespace, sparkline, configAria)}
			{renderTimestampBlock(hideWhitespace, timestamp)}
		</Fragment>
	);
};

const view = commonView(renderScoreAdvanced);

/**
 * An advanced score lets you show:
 * - A number with a header
 * - Metrics on changes over time
 * - The target value and gap
 * - A sparkline chart displaying the trend over time
 * - A timestamp at the bottom
 *
 * By default, it shows whitespace when elements are empty, but can be configured to collapse that whitespace for more compact layouts.
 *
 *
 * ```jsx
 * <now-score-advanced
 *    score="$307.63"
 *    score-size="lg"
 *    heading="ServiceNow stock price"
 *    change-info={{
 *        change: "$25.31 (8.95%)",
 *        arrow: "up",
 *        timestamp: "since Jan 1"
 *    }}
 *    target-info={{
 *        target: "$316.88",
 *        gap: "$9.25 (2.91%)"
 *    }}
 *    sparkline={{
 *      seriesConfig: {
 *          mySpark: {x: 'timeStamp', y: 'y0', type: 'area'}
 *      },
 *      seriesData: [
 *          {timeStamp: 1501843274096, y0: 282.67},
 *          {timeStamp: 1511434701664, y0: 300.00},
 *          {timeStamp: 1521026129232, y0: 307.63}
 *      ]
 *    }}
 *    timestamp="27 Jan 2020"
 * >
 * </now-score-advanced>
 * ```
 *
 * @seismicElement now-score-advanced
 * @summary A score that shows a number and supporting metrics.
 */
createEnhancedElement('now-score-advanced', {
	properties: {
		/**
		 * An object whose keys reference a specific HTML element within the
		 * component. The value of each key is an object that contains
		 * an ARIA property (or properties) and corresponding value/s set on the specified element.
		 * The 'sparkline' key corresponds to the inner `<now-chart-sparkline>`
		 * element within the component. Use this property to supply an
		 * aria-label that describes the chart for screen readers. See
		 * https://www.w3.org/TR/wai-aria-1.1/ for other properties and accepted values.
		 * @type {{ 'aria-*': string }}
		 */
		configAria: {
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							sparkline: configAriaSchema
						},
						additionalProperties: false
					},
					configAriaSchema
				]
			}
		},
		/**
		 * When true, enables score to dispatch a click action.
		 * @type {boolean}
		 */
		canClick: {
			default: false,
			schema: {type: 'boolean'}
		},
		/**
		 * The score title.
		 * @type {string}
		 */
		heading: {
			default: '',
			schema: {type: 'string'}
		},
		/**
		 * The score number.
		 * @type {string}
		 */
		score: {
			default: '',
			schema: {type: 'string'}
		},
		/**
		 * The score size.
		 * @type {string}
		 */
		scoreSize: {
			default: 'md',
			schema: {
				type: 'string',
				enum: ['xs', 'sm', 'md', 'lg', 'xl', 'xxl']
			}
		},
		/**
		 * The score change values.
		 * @type {{change: string, timestamp: string, arrow: string}}
		 */
		changeInfo: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					change: {type: 'string'},
					timestamp: {type: 'string'},
					arrow: {
						schema: {
							type: 'string',
							enum: ['up', 'down', 'neutral']
						}
					}
				}
			}
		},
		/**
		 * The score target values.
		 * @type {{target: string, gap: string}}
		 */
		targetInfo: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					target: {type: 'string'},
					gap: {type: 'string'}
				}
			}
		},
		/**
		 * The score sparkline settings. Please refer to the API documents for
		 * the `now-chart-sparkline` component for a full description of each
		 * supported property.
		 * @type {{extents: Object, seriesConfig: Object, seriesData: Array, targetsConfig: Object, targetsData: Array, thresholdConfig: Object, thresholdData: Array}}
		 */
		sparkline: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					extents: {
						type: 'object',
						patternProperties: {
							'.*': {
								type: 'array',
								items: {oneOf: [{type: 'number'}, {const: 'dynamic'}]},
								minItems: 2,
								maxItems: 2
							}
						}
					},
					seriesConfig: {
						type: 'object',
						patternProperties: {'.*': seriesConfigSchema}
					},
					seriesData: {type: 'array'},
					targetsConfig: {
						type: 'object',
						patternProperties: {'.*': targetSeriesConfigSchema}
					},
					targetsData: {type: 'array'},
					thresholdConfig: {
						type: 'object',
						patternProperties: {'.*': thresholdConfigSchema}
					},
					thresholdData: {type: 'array', items: thresholdDataPointSchema}
				}
			}
		},
		/**
		 * The score timestamp.
		 * @type {string}
		 */
		timestamp: {
			default: '',
			schema: {type: 'string'}
		},
		/**
		 * When true, collapses whitespace for empty values.
		 * @type {boolean}
		 */
		hideWhitespace: {
			default: false,
			schema: {type: 'boolean'}
		}
	},
	view,
	styles,
	dispatches: {
		/**
		 * If the `canClick` property is true, dispaches when a user clicks the score.
		 * @type {{}}
		 */
		'NOW_SCORE#CLICKED': {}
	}
});
