import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-score.scss';
import {commonView, renderScore} from './utils/common';

const renderTopHeading = (hideWhitespace, topHeading) => {
	if (hideWhitespace && !topHeading) {
		return null;
	}

	return (
		<span className="now-score-heading -top">
			<span className="will-truncate">{topHeading}&nbsp;</span>
		</span>
	);
};

const renderBottomHeading = (hideWhitespace, bottomHeading) => {
	if (hideWhitespace && !bottomHeading) {
		return null;
	}

	return (
		<span className="now-score-heading -bottom">
			<span className="will-truncate">{bottomHeading}&nbsp;</span>
		</span>
	);
};

const renderTimestamp = (hideWhitespace, timestamp) => {
	if (hideWhitespace && !timestamp) {
		return null;
	}

	return (
		<span
			class={{
				'now-score-timestamp': true,
				'is-expanded': !hideWhitespace
			}}>
			<span className="will-truncate">{timestamp}</span>
		</span>
	);
};

const renderScoreBasic = (props) => {
	const {
		heading,
		headingPosition,
		score,
		scoreSize,
		timestamp,
		hideWhitespace
	} = props;

	const topHeading = headingPosition === 'top' ? heading : '';
	const bottomHeading = headingPosition === 'bottom' ? heading : '';

	return (
		<Fragment>
			{renderTopHeading(hideWhitespace, topHeading)}
			{renderScore(score, scoreSize)}
			{renderBottomHeading(hideWhitespace, bottomHeading)}
			{renderTimestamp(hideWhitespace, timestamp)}
		</Fragment>
	);
};

const view = commonView(renderScoreBasic);

/**
 * A basic score lets you show a number with supporting text, either before or after the score, and a timestamp at the bottom.
 *
 * By default, it shows whitespace when elements are empty, but can be configured to collapse that whitespace for more compact layouts.
 *
 *
 * ```jsx
 * <now-score-basic
 *    score="42"
 *    score-size="xl"
 *    heading="Ultimate Question to Life, the Universe, and Everything"
 *    heading-position="top"
 *    timestamp="12 October 1979"
 * >
 * </now-score-basic>
 * ```
 *
 * @seismicElement now-score-basic
 * @summary A score that shows a number and supporting text.
 * @uib.innerComponents
 */
createEnhancedElement('now-score-basic', {
	properties: {
		/**
		 * When true, enables score to dispatch a click action.
		 * @type {boolean}
		 */
		canClick: {
			default: false,
			schema: {type: 'boolean'}
		},
		/**
		 * The score title.
		 * @type {string}
		 */
		heading: {
			default: '',
			schema: {type: 'string'}
		},
		/**
		 * The score heading position, above or below the score value.
		 * @type {string}
		 */
		headingPosition: {
			default: 'bottom',
			schema: {
				type: 'string',
				enum: ['top', 'bottom']
			}
		},
		/**
		 * The score number.
		 * @type {string}
		 */
		score: {
			default: '',
			schema: {type: 'string'}
		},
		/**
		 * The score size.
		 * @type {string}
		 */
		scoreSize: {
			default: 'md',
			schema: {
				type: 'string',
				enum: ['xs', 'sm', 'md', 'lg', 'xl', 'xxl']
			}
		},
		/**
		 * The score timestamp.
		 * @type {string}
		 */
		timestamp: {
			default: '',
			schema: {type: 'string'}
		},
		/**
		 * When true, collapses whitespace for empty values.
		 * @type {boolean}
		 */
		hideWhitespace: {
			default: false,
			schema: {type: 'boolean'}
		}
	},
	view,
	styles,
	dispatches: {
		/**
		 * If the `canClick` property is true, dispaches when a user clicks the score.
		 * @type {{}}
		 */
		'NOW_SCORE#CLICKED': {}
	}
});
