import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';
import '@servicenow/now-icon';

const EMPTY_SYMBOL = '--';
const UP_ARROW = 'arrow-up-fill';
const NO_ARROW = 'minus-fill';
const DOWN_ARROW = 'arrow-down-fill';
const ICON_SR_MAPPER = {
	up: 'Trending upward',
	down: 'Trending downward',
	neutral: 'Trending neutral'
};

function commonView(scoreDom) {
	return (state, {dispatch}) => {
		const {canClick} = state.properties;

		if (canClick) {
			return (
				<button
					class={{'now-score': true, 'is-interactive': true}}
					type="button"
					on-click={() => {
						dispatch('NOW_SCORE#CLICKED', {});
					}}>
					{scoreDom(state.properties)}
				</button>
			);
		}

		return <div className="now-score">{scoreDom(state.properties)}</div>;
	};
}

function renderScore(score, scoreSize) {
	const val = score.length === 0 ? EMPTY_SYMBOL : score;
	return (
		<span
			class={{'now-score-value': true, [`-${scoreSize}`]: true}}
			title={val}>
			<span className="will-truncate">{val}&nbsp;</span>
		</span>
	);
}

function getIcon(arrow) {
	if (arrow === 'up') {
		return UP_ARROW;
	}

	if (arrow === 'down') {
		return DOWN_ARROW;
	}

	return NO_ARROW;
}

function renderChange({change, timestamp, arrow}) {
	if (!change) {
		return <span data-test="change">&nbsp;</span>;
	}

	const icon = getIcon(arrow);
	const title = `${change} ${timestamp}`;

	return (
		<span
			data-test="change"
			title={title}
			className="now-score-label"
			aria-label={`${t('{0}', ICON_SR_MAPPER[arrow])} ${title}`}>
			<now-icon className="now-score-label-custom" icon={icon} />
			&nbsp;
			<span className="will-truncate" aria-hidden="true">
				<span className="now-score-label-custom">{change}&nbsp;</span>
				<span className="now-score-label-secondary">{timestamp}</span>
			</span>
		</span>
	);
}

function renderTargetGap({target, gap}) {
	if (!target) {
		return <span data-test="targetgap">&nbsp;</span>;
	}

	const gapTitle = gap ? ` | ${t('Gap')}: ${gap}` : '';
	const title = `${t('Target')}: ${target}${gapTitle}`;

	return (
		<span className="now-score-label" data-test="targetgap" title={title}>
			<span className="will-truncate">
				<span className="now-score-label-secondary">{t('Target')}&nbsp;</span>
				<span className="now-score-label-primary">{target}</span>
				{gap ? (
					<Fragment>
						<span className="now-score-label-primary">&nbsp;|&nbsp;</span>
						<span className="now-score-label-secondary">{t('Gap')}&nbsp;</span>
						<span className="now-score-label-primary">{gap}</span>
					</Fragment>
				) : null}
			</span>
		</span>
	);
}

export {commonView, renderScore, renderChange, renderTargetGap};
